html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
menu,
ol,
pre,
ul,
figure,
dl,
dd,
body,
blockquote {
  margin: 0;
  padding: 0;
}

body {
  color: black;
}

a {
  color: black;
}

a:visited:active {
  color: black;
}

input,
button {
  background-color: white;
  border: 1px solid black;
}

.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.nav:before {
  content: "";
  width: 100vw;
  position: absolute;
  background-color: white;
  height: 55px;
  top: 0;
  left: 0;
  z-index: 90;
}

.navBlack:before {
  background-color: #1d1d1d;
  transition: color 0.4s ease, background-color 0.4s ease;
  height: 55px;
  z-index: 2;
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  z-index: 91;
  margin: 0;
}

.navItems__item {
  max-height: 56px;
  margin-top: 20px;
}

.navItems__item:not(:first-child) {
  margin-left: 40px;
}

.navItems__item-inner {
  text-decoration: none;
  color: #989696;
  transition: color 0.35s ease;
}

.navItems__item-inner:hover {
  color: black;
}

.whiteHover:hover {
  color: white;
}

.hidden {
  display: none;
}

.hidden-mq {
  display: block;
}

.logo {
  text-decoration: none;
  z-index: 99;
  margin: 6px 20px 0 65px !important;
}

.logo-inner {
  box-sizing: border-box;
  padding: 3px 15px;
  font-size: 38px;
  color: white;
  background-color: #1d1d1d;
  border: 2px solid #1d1d1d;
  transition: all 0.4s ease;
}

.logo-inner:hover {
  color: #1d1d1d;
  background-color: white;
  border: 2px solid #1d1d1d;
}

.logoWhite {
  color: black;
  background-color: white;
  border: 2px solid white;
  transition: all 0.4s ease;
}

.logoWhite:hover {
  color: white;
  background-color: #1d1d1d;
  border: 2px solid white;
}

a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.img-buildings {
  z-index: 1;
  position: relative;
  bottom: 0;
  width: 100%;
  background-color: #1d1d1d;
}

.img-person {
  max-width: 170px;
  height: auto;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  margin-bottom: 20px;
}

.img-smallperson {
  max-width: 70px;
  height: auto;
  border-radius: 50%;
  display: block;
  margin-bottom: 20px;
  margin-right: auto !important;
  margin-top: 10px;
}

.img-logo {
  max-width: 55px;
  height: auto;
}

.img-cover {
  position: absolute;
  width: 100%;
  object-fit: contain;
  bottom: 0px;
  top: 24%;
  left: 51%;
  max-width: 55%;
  opacity: 0.5;
  max-height: 100%;
  transform: translate(-50%);
  z-index: -4;
}

.img-current {
  max-width: 38px;
  height: auto;
}

.img-current:hover {
  background-color: white;
}

.img-small {
  max-width: 20px;
  height: auto;
}

.svg-current {
  fill: white;
  width: 10px;
}

.svg-current:hover {
  fill: white;
}

.title {
  font-size: 44px;
  font-weight: normal;
  margin: 0 auto 7px;
  text-align: center;
  max-width: 85%;
}

.title-right {
  text-align: end;
}

.title-white {
  color: white;
}

.title-left {
  text-align: start;
}

.title-mb {
  margin-bottom: 15px;
}

.title-small {
  font-size: 20px;
  text-align: center;
}

.subtitle {
  font-size: 16px;
  font-weight: normal;
  margin: 0 auto;
  text-align: center;
  max-width: 85%;
}

.subtitle-white {
  color: white;
}

.subtitle-small {
  font-size: 14px;
  color: grey;
  text-align: center;
}

.subtitle-right {
  text-align: end;
  margin-left: auto;
  max-width: 80%;
}

.subtitle-left {
  text-align: start;
  margin-right: auto;
  max-width: 80%;
}

.btn {
  text-decoration: none;
  color: black;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
  padding: 15px 40px;
  transition: all 0.4s ease;
  background-color: white;
  cursor: pointer;
}

.btn:hover {
  color: white;
  background-color: black;
  border-radius: 50px;
}

.btn-black {
  color: white;
  background-color: black;
  border: none;
  max-width: 90%;
  width: 80%;
  padding: 10px 0 !important;
}

.btn-white {
  background-color: white;
  display: block;
  position: relative;
  max-width: 177px;
  margin: 100px auto 0;
}

.btn-white:hover {
  border-radius: 100px;
  color: black;
  background-color: white;
}

.wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.wrapper-mq {
  display: flex;
  align-items: center;
}

.fa {
  text-align: center;
  font-size: 25px;
  margin: 0 auto 5px;
}

* {
  box-sizing: border-box;
  outline: none;
  font-family: Roboto;
  font-size: 15px;
}

body {
  overflow-x: hidden;
}

.main {
  z-index: 0;
}

.promo {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.textPromo {
  max-width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.textPromo__title {
  font-weight: lighter !important;
}

.textPromo__subtitle {
  font-family: "Merriweather", serif;
  font-style: italic;
  margin-top: 10px !important;
}

.textPromo__btn {
  border: 1px solid black;
  margin-top: 50px;
  max-width: 190px;
}

.textPromo__arrowDown {
  display: block;
  position: absolute;
  bottom: 2%;
  font-size: 28px;
  transition: all 0.3s ease;
  z-index: 2;
}

.textPromo__arrowDown:hover {
  transform: scale(1.2);
}

.textWrap {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timeline {
  background-color: #1d1d1d;
  z-index: -2;
  width: 100%;
  height: 100%;
  position: relative;
}

.line {
  display: block;
  width: 5px;
  height: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 2;
}

.yearsCont {
  max-width: 980px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 30px;
}

.yearCont {
  display: flex;
  max-width: 400px;
  width: 100%;
  height: auto;
}

.yearCont-left {
  margin-right: auto;
  margin-top: 100px;
  justify-content: flex-end;
}

.yearCont-right {
  margin-left: auto;
  margin-top: 100px;
  justify-content: flex-start;
}

.textCont {
  display: flex;
  flex-direction: column;
}

.textCont-left {
  order: 1;
}

.textCont-right {
  order: 2;
}

.textCont__title {
  color: white;
  max-width: 90%;
  width: 100%;
}

.textCont__subtitle {
  color: white;
  max-width: 90%;
}

.colored {
  background-color: #a13232;
}

.circle {
  color: white;
  position: absolute;
  font-size: 25px;
  z-index: 3;
}

.circle-left {
  order: 1;
  left: 50%;
  transform: translate(-50%);
}

.circle-right {
  order: 2;
  left: 50%;
  transform: translate(-50%);
}

.team {
  background-color: white;
  z-index: -2;
  width: 100%;
  height: 100%;
  position: relative;
  padding-top: 60px;
  bottom: 5px;
  padding-bottom: 450px;
}

.persons {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 100px;
}

.person {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  margin: auto;
  margin-bottom: 60px;
}

.person__text {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.social {
  display: flex;
  justify-content: center;
  position: relative;
  flex-wrap: wrap;
}

.social .fa {
  margin: 0 10px;
  font-size: 20px;
  cursor: pointer;
  position: relative;
  z-index: 1000 !important;
}

.lower {
  position: relative;
  top: 70px;
}

.statsCont {
  background-color: #1d1d1d;
  z-index: 0;
  width: 100%;
  height: 100%;
  padding-top: 0px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.statsCont::before {
  background: inherit;
  content: "";
  display: block;
  height: 60%;
  bottom: 90%;
  width: 100%;
  left: 0;
  position: absolute;
  background-color: #1d1d1d;
  right: 0;
  z-index: -4;
  -webkit-backface-visibility: hidden;
  transform: skewY(-5deg);
  transform-origin: 100%;
}

.statsCont:after {
  background: inherit;
  content: "";
  display: block;
  height: 73%;
  top: 50%;
  left: 0;
  position: absolute;
  background-color: #1d1d1d;
  right: 0;
  z-index: -5;
  -webkit-backface-visibility: hidden;
  bottom: 0;
  transform: skewY(-5deg);
  transform-origin: 100%;
}

.contact {
  background-color: white;
  z-index: -2;
  width: 100%;
  height: 100vh;
  padding-top: 350px;
}

.stats {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 120px;
}

.stat {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  margin-bottom: 60px;
  justify-content: center;
  text-align: center;
}

.contactsBlock {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 100px;
}

.contactItem {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  margin-bottom: 80px;
  justify-content: center;
  text-align: center;
}

.form {
  max-width: 780px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.input {
  margin: 10px 0;
  padding: 7px 0;
  font-size: 20px;
  max-width: 90%;
  min-width: 80%;
  text-align: center;
  border: 1px solid #8a8a8a;
}

.input::placeholder {
  color: #b8b8b8;
  text-align: center;
}

.textarea {
  padding: 10px;
  margin: 10px 0;
  max-width: 90%;
  min-width: 80%;
  border: 1px solid #8a8a8a;
  resize: none;
  font-size: 20px;
}

.textarea::placeholder {
  color: #b8b8b8;
  text-align: start;
}

.footer {
  max-width: 90%;
  width: 100%;
  margin: 10px auto;
  text-align: center;
}

.posts {
  margin-top: 100px;
  max-width: 1200px;
  margin: 120px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.post {
  margin-top: 60px;
  background-color: black;
  padding: 30px;
  max-width: 90%;
}

.author {
  font-weight: bold !important;
  font-size: 20px !important;
  text-align: start !important;
  margin-right: auto !important;
  margin-top: 30px !important;
  margin-left: 80px !important;
  color: white;
}

.text {
  max-width: 90%;
  color: white;
  margin-top: 30px !important;
}

.headingText {
  font-size: 40px !important;
  max-width: 1200px;
  text-align: center;
  width: 100%;
  margin: 0px auto;
}

.fa {
  color: #313131;
  cursor: pointer;
}

.team {
  z-index: 0 !important;
}

.socialIcon {
  margin-bottom: 12px !important;
}

.socialIcon:before {
  font-size: 38px;
}

@media screen and (max-width: 1900px) {
  .statsCont::before {
    bottom: 75%;
  }
}

@media screen and (max-width: 960px) {
  .img-cover {
    top: 20%;
    left: 53%;
  }

  .img-cover {
    max-width: 80%;
  }

  .btn-white {
    margin-top: 0px;
  }

  .team {
    padding-bottom: 200px;
  }

  .persons {
    justify-content: center;
  }

  .person {
    width: calc(83.33333% - 30px);
  }

  .person {
    margin-top: 40px;
  }

  .statsCont::before {
    height: 250px;
    bottom: 92%;
  }

  .statsCont:after {
    height: 450px;
    top: 75%;
  }

  .stats {
    justify-content: center;
  }

  .stat {
    width: calc(83.33333% - 30px);
  }

  .contactsBlock {
    justify-content: center;
  }

  .contactItem {
    width: calc(50% - 30px);
  }

  .input {
    font-size: 16px;
  }

  .textarea {
    font-size: 16px;
  }
}

@media only screen and (max-width: 960px) {
  .stats {
    margin-top: 80px;
  }
}

@media screen and (max-width: 900px) {
  .yearsCont {
    width: 100%;
    max-width: 100vw;
    margin: 0;
  }

  .yearCont {
    max-width: 330px;
  }

  .yearCont-left,
  .yearCont-right {
    margin-top: 80px;
  }
}

@media only screen and (max-width: 900px) {
  .contact {
    padding-top: 300px !important;
  }

  .contactItem {
    margin-bottom: 60px;
  }
}

@media only screen and (max-width: 767px) {
  .nav:before {
    height: 51px;
  }

  .navBlack:before {
    height: 50px;
  }

  .logo {
    margin: 0 !important;
    position: relative;
    top: 1px !important;
  }

  .scrollLock {
    overflow: hidden;
  }

  .hidden {
    display: block;
  }

  .hidden-mq {
    display: none;
  }

  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 99;
    margin: 0;
    cursor: pointer;
  }

  .active:before {
    transform: rotate(45deg);
    top: 12px !important;
  }

  .active:after {
    transform: rotate(-45deg);
    bottom: 10px !important;
  }

  .active > span {
    transform: scale(0);
  }

  .burger:before,
  .burger:after,
  .burger > span {
    content: "";
    background-color: black;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 100px;
  }

  .burger > span {
    top: 11px;
    transition: all 0.2s ease;
  }

  .burger:before {
    top: 0;
    transition: all 0.35s ease;
  }

  .burger:after {
    bottom: 0;
    transition: all 0.35s ease;
  }

  .wrapper-mq {
    justify-content: space-between;
    background-color: white;
    padding: 0 15px;
    z-index: 98;
    width: 100%;
    max-height: 51px;
  }

  .navCont {
    position: fixed;
    top: 0;
    right: -100%;
    max-width: 100vw;
    width: 400px;
    height: 100%;
    background: #131313;
    padding-top: 40px;
    z-index: 10;
    display: flex;
    justify-content: center;
    transition: all 0.6s ease;
    overflow: auto;
  }

  .active {
    right: 0;
  }

  .navItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: center;
  }

  .navItems__item {
    margin-left: 0 !important;
    margin-top: 45px;
  }

  .navItems__item:last-child {
    margin-bottom: 40px;
  }

  .navItems__item-inner {
    font-size: 20px;
    color: white;
    font-weight: lighter;
  }

  .navItems__item-inner:hover {
    color: white;
  }
}

@media screen and (max-width: 760px) {
  .img-cover {
    top: 40%;
  }
}

@media screen and (max-width: 750px) {
  .yearCont {
    max-width: 250px;
  }
}

@media only screen and (max-width: 700px) {
  .line {
    width: 3px;
  }
}

@media screen and (max-width: 600px) {
  .img-cover {
    top: 60%;
  }

  .img-current {
    max-width: 25px;
  }

  .img-small {
    max-width: 16px;
  }

  .title {
    font-size: 27px;
  }

  .subtitle {
    font-size: 13px;
  }

  .btn {
    font-size: 13px;
  }

  .yearCont-left,
  .yearCont-right {
    margin-top: 50px;
  }

  .textCont__subtitle {
    max-width: 90%;
  }
}

@media screen and (max-width: 560px) {
  .team {
    padding-bottom: 150px;
  }

  .person {
    width: calc(83.33333% - 30px);
  }

  .stat {
    width: calc(83.33333% - 30px);
  }

  .contactItem {
    width: calc(83.33333% - 30px);
  }
}

@media only screen and (max-width: 560px) {
  .contactsBlock {
    margin-top: 50px;
  }

  .contactItem {
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 555px) {
  .yearCont {
    max-width: 200px;
  }
}

@media screen and (max-width: 500px) {
  .img-cover {
    top: 70%;
  }
}

@media only screen and (max-width: 500px) {
  .line {
    width: 2px;
  }

  .contact {
    padding-top: 250px !important;
  }
