$navColor: rgb(255, 255, 255);
$logoColor: black;
$logoColor_hover: rgb(37, 100, 216);
$navItemColor: rgb(152, 150, 150);
$navItemColor_hover: black;
$burgerColor: black;
$MQ_menuColor: rgb(19, 19, 19);
$bc: #1d1d1d;

.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: center;
  width: 100%;

  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  &:before {
    content: "";
    width: 100vw;
    position: absolute;
    background-color: $navColor;
    height: 55px;
    top: 0;
    left: 0;
    z-index: 90;
    //box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.5);

    @media only screen and (max-width: 767px) {
      height: 51px;
    }
  }
}

.navBlack {
  &:before {
    background-color: $bc;
    transition: color 0.4s ease, background-color 0.4s ease;
    height: 55px;
    z-index: 2;

    @media only screen and (max-width: 767px) {
      height: 50px;
    }
  }
}

.navItems {
  display: flex;
  list-style: none;
  position: relative;
  padding: 0;
  z-index: 91;
  margin: 0;

  &__item {
    max-height: 56px;
    margin-top: 20px;
    &:not(:first-child) {
      margin-left: 40px;
    }

    &-inner {
      text-decoration: none;

      color: $navItemColor;
      transition: color 0.35s ease;

      &:hover {
        color: black;
      }
    }
  }
}
.whiteHover {
  &:hover {
    color: white;
  }
}

.hidden {
  display: none;
}

.hidden-mq {
  display: block;
}

@media only screen and (max-width: 767px) {
  .logo {
    margin: 0 !important;
    position: relative;
    top: 1px !important;
  }

  .scrollLock {
    overflow: hidden;
  }

  .hidden {
    display: block;
  }

  .hidden-mq {
    display: none;
  }

  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 99;
    margin: 0;
    cursor: pointer;
  }

  .active:before {
    transform: rotate(45deg);
    top: 12px !important;
  }

  .active:after {
    transform: rotate(-45deg);
    bottom: 10px !important;
  }

  .active > span {
    transform: scale(0);
  }

  .burger:before,
  .burger:after,
  .burger > span {
    content: "";
    background-color: $burgerColor;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 100px;
  }

  .burger > span {
    top: 11px;
    transition: all 0.2s ease;
  }

  .burger:before {
    top: 0;
    transition: all 0.35s ease;
  }

  .burger:after {
    bottom: 0;
    transition: all 0.35s ease;
  }

  .wrapper-mq {
    justify-content: space-between;
    background-color: $navColor;
    padding: 0 15px;
    z-index: 98;
    width: 100%;
    max-height: 51px;
  }

  .navCont {
    position: fixed;
    top: 0;
    right: -100%;
    max-width: 100vw;
    width: 400px;
    height: 100%;
    background: $MQ_menuColor;
    padding-top: 40px;
    z-index: 10;
    display: flex;
    justify-content: center;
    transition: all 0.6s ease;
    overflow: auto;
  }

  .active {
    right: 0;
  }

  .navItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: center;

    &__item {
      margin-left: 0 !important;
      margin-top: 45px;

      &:last-child {
        margin-bottom: 40px;
      }

      &-inner {
        font-size: 20px;
        color: white;
        font-weight: lighter;

        &:hover {
          color: white;
        }
      }
    }
  }
}
