@import "./helpers/fonts.scss";
@import "./helpers/normalize.scss";
@import "./helpers/smart-grid.scss";
@import "./helpers/variables.scss";
@import "./helpers/mixins.scss";
@import "../modules/_header/header.scss";
@import "../modules/_sidebar/sidebar.scss";
@import "../modules/_footer/footer.scss";
@import "./vendor.scss";

$bc: #1d1d1d;

* {
  box-sizing: border-box;
  outline: none;
  font-family: Roboto;
  font-size: 15px;
}

body {
  overflow-x: hidden;
}

.main {
  // max-width: 1200px;
  z-index: 0;
}

.promo {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.textPromo {
  max-width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  &__title {
    font-weight: lighter !important;

    @media only screen and (max-width: 400px) {
      font-size: 23.5px;
    }
  }

  &__subtitle {
    font-family: "Merriweather", serif;
    font-style: italic;
    margin-top: 10px !important;
  }

  &__btn {
    border: 1px solid black;
    margin-top: 50px;
    max-width: 190px;
  }

  &__arrowDown {
    display: block;
    position: absolute;
    bottom: 2%;
    font-size: 28px;
    transition: all 0.3s ease;

    z-index: 2;

    &:hover {
      transform: scale(1.2);
    }
  }
}

.textWrap {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timeline {
  background-color: $bc;
  z-index: -2;
  width: 100%;
  height: 100%;
  position: relative;
}

.line {
  display: block;
  width: 5px;
  height: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;

  left: 50%;
  transform: translate(-50%, 0);
  z-index: 2;
  @media only screen and (max-width: 700px) {
    width: 3px;
  }

  @media only screen and (max-width: 500px) {
    width: 2px;
  }
}

.yearsCont {
  max-width: 980px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 30px;
  @media screen and (max-width: 900px) {
    width: 100%;
    max-width: 100vw;
    margin: 0;
  }

  // &-left {
  //   margin-right: 35px;
  // }
  // &-right {
  //   margin-left: 35px;
  // }

  // @media screen and (max-width: 600px) {
  //   &-left {
  //     margin-right: 20px;
  //   }
  //   &-right {
  //     margin-left: 20px;
  //   }
  // }
}

.yearCont {
  display: flex;
  max-width: 400px;
  width: 100%;
  height: auto;

  @media screen and (max-width: 900px) {
    max-width: 330px;
  }

  @media screen and (max-width: 750px) {
    max-width: 250px;
  }

  @media screen and (max-width: 555px) {
    max-width: 200px;
  }
  @media screen and (max-width: 455px) {
    max-width: 150px;
  }
  @media screen and (max-width: 355px) {
    max-width: 130px;
  }

  &-left {
    margin-right: auto;
    margin-top: 100px;
    justify-content: flex-end;
  }
  &-right {
    margin-left: auto;
    margin-top: 100px;
    justify-content: flex-start;
  }

  @media screen and (max-width: 900px) {
    &-left,
    &-right {
      margin-top: 80px;
    }
  }

  @media screen and (max-width: 600px) {
    &-left,
    &-right {
      margin-top: 50px;
    }
  }
}

.textCont {
  display: flex;
  flex-direction: column;

  &-left {
    order: 1;
  }

  &-right {
    order: 2;
  }

  &__title {
    color: white;

    max-width: 90%;
    width: 100%;
  }
  &__subtitle {
    color: white;
    max-width: 90%;

    @media screen and (max-width: 600px) {
      max-width: 90%;
    }
  }
}

.colored {
  background-color: rgb(161, 50, 50);
}

.circle {
  color: white;
  position: absolute;
  font-size: 25px;
  z-index: 3;

  &-left {
    order: 1;
    left: 50%;
    transform: translate(-50%);
  }
  &-right {
    order: 2;
    left: 50%;
    transform: translate(-50%);
  }
}

.team {
  background-color: rgb(255, 255, 255);
  z-index: -2;
  width: 100%;
  height: 100%;
  position: relative;
  padding-top: 60px;
  bottom: 5px;
  padding-bottom: 450px;

  @media screen and (max-width: 960px) {
    padding-bottom: 200px;
  }
  @media screen and (max-width: 560px) {
    padding-bottom: 150px;
  }
}

.persons {
  // display: flex;
  // flex-wrap: wrap;
  // justify-content: center;
  @include row-flex();
  @include md(justify-content, center);

  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 100px;
}

.person {
  @include col();
  @include size(4);
  @include size-md(10);
  @include size-xs(10);
  margin: auto;
  margin-bottom: 60px;

  @media screen and (max-width: 960px) {
    margin-top: 40px;
  }

  &__text {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
  }
}

.social {
  display: flex;
  justify-content: center;
  position: relative;
  flex-wrap: wrap;

  .fa {
    margin: 0 10px;
    font-size: 20px;
    cursor: pointer;
    position: relative;
    z-index: 1000 !important;
  }
}

.lower {
  position: relative;
  top: 70px;
}

.statsCont {
  background-color: $bc;
  z-index: 0;
  width: 100%;
  height: 100%;
  @media screen and (max-width: 960px) {
  }

  padding-top: 0px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // padding-bottom: 200px;
  @media screen and (max-width: 960px) {
    //padding-bottom: 330px;
  }

  &::before {
    background: inherit;
    content: "";
    display: block;
    height: 60%;
    bottom: 90%;
    width: 100%;

    @media screen and (max-width: 1900px) {
      bottom: 75%;
    }

    @media screen and (max-width: 960px) {
      height: 250px;
      bottom: 92%;
    }

    left: 0;
    position: absolute;
    background-color: $bc;
    right: 0;
    z-index: -4;
    -webkit-backface-visibility: hidden; // for Chrome Windows
    transform: skewY(-5deg);
    transform-origin: 100%;
  }

  &:after {
    background: inherit;
    content: "";
    display: block;
    height: 73%;

    top: 50%;
    @media screen and (max-width: 960px) {
      height: 450px;
      top: 75%;
    }
    left: 0;
    position: absolute;
    background-color: $bc;
    right: 0;
    z-index: -5;
    -webkit-backface-visibility: hidden; // for Chrome Windows
    bottom: 0;
    transform: skewY(-5deg);
    transform-origin: 100%;
  }
}

.contact {
  background-color: white;
  z-index: -2;
  width: 100%;
  height: 100vh;
  padding-top: 350px;
  @media only screen and (max-width: 900px) {
    padding-top: 300px !important;
  }

  @media only screen and (max-width: 500px) {
    padding-top: 250px !important;
  }
}

.stats {
  @include row-flex();
  @include md(justify-content, center);

  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 120px;
  @media only screen and (max-width: 960px) {
    margin-top: 80px;
  }
}

.stat {
  @include col();
  @include size(4);
  @include size-md(10);
  @include size-xs(10);
  margin-bottom: 60px;
  justify-content: center;
  text-align: center;
}

.contactsBlock {
  @include row-flex();
  @include md(justify-content, center);

  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 100px;
  @media only screen and (max-width: 560px) {
    margin-top: 50px;
  }
}

.contactItem {
  @include col();
  @include size(4);
  @include size-md(6);
  @include size-xs(10);
  margin-bottom: 80px;
  justify-content: center;
  text-align: center;
  @media only screen and (max-width: 900px) {
    margin-bottom: 60px;
  }

  @media only screen and (max-width: 560px) {
    margin-bottom: 50px;
  }
}

.form {
  max-width: 780px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.input {
  margin: 10px 0;
  padding: 7px 0;
  font-size: 20px;
  max-width: 90%;
  min-width: 80%;
  text-align: center;
  border: 1px solid rgb(138, 138, 138);

  @media screen and (max-width: 960px) {
    font-size: 16px;
  }
}

.input::placeholder {
  color: rgb(184, 184, 184);
  text-align: center;
}

.textarea {
  padding: 10px;
  margin: 10px 0;
  max-width: 90%;
  min-width: 80%;
  border: 1px solid rgb(138, 138, 138);
  resize: none;
  font-size: 20px;
  @media screen and (max-width: 960px) {
    font-size: 16px;
  }
  @media only screen and (max-width: 350px) {
    max-width: 80% !important;
  }
}

.textarea::placeholder {
  color: rgb(184, 184, 184);
  text-align: start;
}

.footer {
  max-width: 90%;
  width: 100%;
  margin: 10px auto;
  text-align: center;
}

.posts {
  margin-top: 100px;
  max-width: 1200px;
  margin: 120px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.post {
  margin-top: 60px;
  background-color: rgb(0, 0, 0);
  padding: 30px;
  max-width: 90%;
}

.author {
  font-weight: bold !important;
  font-size: 20px !important;
  text-align: start !important;
  margin-right: auto !important;
  margin-top: 30px !important;
  margin-left: 80px !important;
  color: white;
}

.text {
  max-width: 90%;
  color: white;
  margin-top: 30px !important;
}

.headingText {
  font-size: 40px !important;
  max-width: 1200px;
  text-align: center;
  width: 100%;
  margin: 0px auto;
}

.fa {
  color: rgb(49, 49, 49);
  cursor: pointer;
}

.team {
  z-index: 0 !important;
}

.socialIcon {
  margin-bottom: 12px !important;
  &:before {
    font-size: 38px;
  }
}
