$bc: #1d1d1d;

.logo {
  text-decoration: none;
  z-index: 99;
  margin: 6px 20px 0 65px !important;

  &-inner {
    box-sizing: border-box;
    padding: 3px 15px;
    font-size: 38px;
    //font-weight: bold;
    color: white;
    background-color: $bc;
    border: 2px solid $bc;
    transition: all 0.4s ease;

    &:hover {
      color: $bc;
      background-color: white;

      border: 2px solid $bc;
    }
  }
}

.logoWhite {
  color: rgb(0, 0, 0);
  background-color: white;
  border: 2px solid white;
  transition: all 0.4s ease;

  &:hover {
    color: white;
    background-color: $bc;
    border: 2px solid white;
  }
}

a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.img {
  &-buildings {
    z-index: 1;
    position: relative;
    bottom: 0;
    width: 100%;
    background-color: $bc;
  }

  &-person {
    max-width: 170px;
    height: auto;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  &-smallperson {
    max-width: 70px;
    height: auto;
    border-radius: 50%;
    display: block;
    margin-bottom: 20px;
    margin-right: auto !important;
    margin-top: 10px;
  }

  &-logo {
    max-width: 55px;
    height: auto;
  }

  &-cover {
    position: absolute;
    width: 100%;
    object-fit: contain;
    bottom: 0px;
    top: 24%;
    left: 51%;

    @media screen and (max-width: 960px) {
      top: 20%;
      left: 53%;
    }

    @media screen and (max-width: 760px) {
      top: 40%;
    }
    @media screen and (max-width: 600px) {
      top: 60%;
    }
    @media screen and (max-width: 500px) {
      top: 70%;
    }
    @media screen and (max-width: 450px) {
      top: 74%;
    }
    @media screen and (max-width: 360px) {
      top: 82%;
    }
    max-width: 55%;
    opacity: 0.5;
    max-height: 100%;
    transform: translate(-50%);
    z-index: -4;
    @media screen and (max-width: 960px) {
      max-width: 80%;
    }
  }

  &-current {
    max-width: 38px;
    height: auto;

    &:hover {
      background-color: white;
    }

    @media screen and (max-width: 600px) {
      max-width: 25px;
    }
  }

  &-small {
    max-width: 20px;
    height: auto;

    @media screen and (max-width: 600px) {
      max-width: 16px;
    }
  }
}

.svg {
  &-current {
    fill: white;
    width: 10px;
    &:hover {
      fill: white;
    }
  }
}

.title {
  font-size: 44px;
  font-weight: normal;
  margin: 0 auto 7px;
  text-align: center;
  max-width: 85%;

  &-right {
    text-align: end;
  }
  &-white {
    color: white;
  }
  &-left {
    text-align: start;
  }

  &-mb {
    margin-bottom: 15px;
  }

  &-small {
    font-size: 20px;
    text-align: center;
  }

  @media screen and (max-width: 600px) {
    font-size: 27px;
  }
}

.subtitle {
  font-size: 16px;
  font-weight: normal;
  margin: 0 auto;
  text-align: center;
  max-width: 85%;

  &-white {
    color: white;
  }

  &-small {
    font-size: 14px;
    color: grey;
    text-align: center;
  }

  &-right {
    text-align: end;
    margin-left: auto;
    max-width: 80%;
  }
  &-left {
    text-align: start;
    margin-right: auto;
    max-width: 80%;
  }

  @media screen and (max-width: 600px) {
    font-size: 13px;
  }
}

.btn {
  text-decoration: none;
  color: black;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
  padding: 15px 40px;
  transition: all 0.4s ease;
  background-color: white;
  cursor: pointer;

  &:hover {
    color: white;
    background-color: black;
    border-radius: 50px;
  }

  &-black {
    color: white;
    background-color: black;
    border: none;
    max-width: 90%;
    width: 80%;
    padding: 10px 0 !important;
  }

  &-white {
    background-color: white;
    display: block;
    position: relative;
    max-width: 177px;
    margin: 100px auto 0;

    @media screen and (max-width: 960px) {
      margin-top: 0px;
    }

    &:hover {
      border-radius: 100px;
      color: rgb(0, 0, 0);
      background-color: rgb(255, 255, 255);
    }
  }

  @media screen and (max-width: 600px) {
    font-size: 13px;
  }
}

.wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;

  &-mq {
    display: flex;
    align-items: center;
  }
}

.fa {
  text-align: center;
  font-size: 25px;
  margin: 0 auto 5px;
}
